.spinner {
  position: absolute;
  width: 50px;
  top: 100px;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  z-index: 10000;
  opacity: 1;
  transition: all 0.3s cubic-bezier(0.24, 0.63, 0.5, 0.99); }
  .spinner .circle {
    position: absolute;
    top: 0;
    left: 0;
    background-color: transparent;
    border: 5px solid transparent;
    border-right: 5px solid #4fd5ca;
    border-left: 5px solid #4fd5ca;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    animation: mainSpin 2s infinite ease-in-out; }
  .spinner .circle1 {
    position: absolute;
    top: 12px;
    left: 12px;
    background-color: transparent;
    border: 2px solid #007c96;
    opacity: 1;
    border-right: 2px solid transparent;
    border-left: 2px solid transparent;
    border-radius: 50%;
    width: 26px;
    height: 26px;
    animation: mainSpinInner 2s infinite ease-in-out; }

.spinner.hidden {
  opacity: 0;
  z-index: 0; }

@keyframes mainSpin {
  0% {
    transform: rotate(0deg); }
  50% {
    transform: rotate(180deg); }
  100% {
    transform: rotate(359deg); } }

@keyframes mainSpinInner {
  0% {
    transform: rotate(0deg); }
  50% {
    transform: rotate(-180deg); }
  100% {
    transform: rotate(-359deg); } }

